import React from 'react'

const Design = () => (
  <div>
    <h1>Design</h1>
      <p>Looking for ways to enhance your web page? or make your app a more happening one? We have
      just the solution for you. You don’t need to fret about it. We try to create an impact with words,
      applications and features that enhance the power of your website and makes it from ordinary to extra ordinary.
      Many a times you do not require any complex feature to make waves, doing simpler things in an efficient and
      effective way can boost your business in the most perfect way possible. Our services include the following features.
      We are also open to ideas and we would like you to take a look at our services for your convenience.</p>
  </div>
)

export default Design



